.NotesScene {
  display: flex;
  flex-direction: column;
  padding-left: 25px !important;

  .notes-upper {
    padding-left: 10px;
   
    h1 {
      margin-bottom: 20px;
    }
  }

  .notes-lower {
    overflow: hidden;
    margin-top: 20px;
    display: grid;
    grid-template-columns: 300px auto;
    grid-gap: 50px;
    flex-grow: 1;
  }
}